const ADD_DIALOGS_NAME_AND_MESSAGE = 'ADD-DIALOGS-NAME-AND-MESSAGE';
// const UPDATE_NEW_DIALOGS_MESSAGE_TEXT = 'UPDATE-NEW-DIALOGS-MESSAGE-TEXT';
// const UPDATE_NEW_DIALOGS_NAME_TEXT = 'UPDATE-NEW-DIALOGS-NAME-TEXT';


let initialState = {
    dialogs: [
        {id: 0, name: 'Dimych'},
        {id: 1, name: 'Andrey'},
        {id: 2, name: 'Sveta'},
        {id: 3, name: 'Sasha'},
        {id: 4, name: 'Viktor'},
        {id: 5, name: 'Valeriy'}
    ],

    messages: [
        {id: 0, message: 'Hi'},
        {id: 1, message: 'Yo'},
        {id: 2, message: 'How are you'},
        {id: 3, message: 'Yo'},
        {id: 4, message: 'What is you name?'}
    ]
}

export const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
         // case UPDATE_NEW_DIALOGS_MESSAGE_TEXT: {
         //     return {
         //         ...state,
         //         newDialogsMessageText: action.newDialogsMessageText
         //     }
         // }
        // case UPDATE_NEW_DIALOGS_NAME_TEXT: {
        //     return {
        //         ...state,
        //         newDialogsNameText: action.newName
        //     }
        // }
        case ADD_DIALOGS_NAME_AND_MESSAGE: {

            let newDialogsMessage = action.newDialogsMessageText;


            // let newDialogsName = {
            //      id: 6,
            //     name: action.newDialogsNameText
            // }

            return {
                ...state,
                // dialogs: [...state.dialogs,  newDialogsName],
                messages: [...state.messages, {id: 5, message: newDialogsMessage}]
            }
        }
        default:
            return state;                /*newDialogsNameText,*/
    }
}


export const addDialogAndMessageActionCreator = ( newDialogsMessageText) =>
    ({type: ADD_DIALOGS_NAME_AND_MESSAGE, newDialogsMessageText})

