import {profileAPI, usersAPI as userAPI} from "../api/api";

const ADD_POST = 'ADD-POST';
// const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';


let initialState = {
    posts: [
        {id: 0, post: 'Hi! How are you?', likesCount: 12},
        {id: 1, post: 'It is my first post!', likesCount: 100},
        {id: 2, post: 'Blabla!', likesCount: 28},
        {id: 3, post: 'It is my second post!', likesCount: 96},
        {id: 4, post: 'Cool!', likesCount: 15},
        {id: 5, post: 'You are Welcome!', likesCount: 54}
    ],

    profile: null,
    status: ""
}


export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        // case UPDATE_NEW_POST_TEXT: {
        //     return {
        //         ...state,
        //         newPostText: action.newText
        //     }
        // }
        case ADD_POST: {

            let newPost = {
                id: 6,
                post: action.newPostText,
                likesCount: 0
            };

            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            }
        }
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
            case DELETE_POST: {
              return {
                  ...state,
                  posts: state.posts.filter(p => p.id != action.postId)
              }
            }

        default:
            return state
    }
}

export const addPostActionCreator = (newPostText) => ({type: ADD_POST, newPostText})                  /*action creator*/

// export const updateNewPostTextActionCreator =
// (text) => ({type: UPDATE_NEW_POST_TEXT, newText: text});                                           /*action creator*/

export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile });                      /*action creator*/

export const setStatus = (status) => ({type: SET_STATUS, status});

export  const deletePost = (postId) => ({type: DELETE_POST, postId});                                 /*action creator*/

export const getUserProfile = (userId) => async (dispatch) => {                                        /*thank creator*/
    let response = await userAPI.getProfile(userId)                                            /*async await (урок 90)*/
    dispatch(setUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {                                                  /*thank creator*/
    let response = await profileAPI.getStatus (userId);
    dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async  (dispatch) => {                                               /*thank creator*/
    let response = await profileAPI.updateStatus(status);
    if (response.data.resultCod === 0) {
        dispatch(setStatus(status));
    }
}


