import * as axios from "axios";


const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "583f6d53-e1e5-4788-bc99-443ecbea87ff"
    }
});

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 5) {

        return instance.get(`users?page=${currentPage}&count=${pageSize}`)       /*get параметр урок 72*/
            .then(response => {
                return response.data;                                            /*почитать о promise (Урок 63)*/
            });
    },

    follow(userId) {
        return instance.post(`follow/${userId}`)                                  /*URI параметр - часть URL  урок 72*/
    },


     unfollow(userId) {
         return instance.delete(`follow/${userId}`)
     }
     ,

     getProfile(userId) {
        console.warn('Obsolet method. Please profileAPI object.');                /*BWC - урок 73*/
         return profileAPI.getProfile(userId);

     }
}

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId);
    },
    getStatus(userId) {
        return instance.get(`profile/status/` + userId);
    },
    updateStatus( status) {
        return instance.put(`profile/status/`, {status: status});        /*put - запрос отправляет на сервер объект*/
    }
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`);                                            /*настройка запроса (урок 61)*/
    },
    login(email, password, rememberMe = false) {
        return instance.post('auth/login', {email, password, rememberMe});          /*login и logout (урок 78)*/
    },
    logout() {
        return instance.delete('auth/login');
    }
}