import React from 'react';
import './App.css';
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Setting from "./components/Setting/Setting";
import UsersContainer from "./components/Users/UsersContainer";
import {Route, withRouter} from "react-router-dom";
// import DialogsContainer from "./components/Dialogs/DialogsContainer";
import NavbarContainer from "./components/Navbar/NavbarContainer";
// import ProfileContainer from "./components/Profile/ProfileContainer";
import HeaderContainer from "./components/Heder/HeaderContainer";
import LoginPage from "./components/Login/Login";
import {connect} from "react-redux";
import {compose} from "redux";
import Preloader from "./components/common/Preloader/Preloader";
import {initializeApp} from "./redux/app-reducer"
import {withSuspense} from "./hoc/withSuspense";

const DialogsContainer = React.lazy(() => import("./components/Dialogs/DialogsContainer"));
const ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainer"));

class App extends React.Component {

    componentDidMount() {
        this.props.initializeApp();
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }
        return (
            < div className='app-wrapper'>
                <HeaderContainer/>
                <NavbarContainer store={this.props.store}/>

                <div className='app-wrapper-content'>
                    < Route path='/news' component={News}/>
                    < Route path='/music' component={Music}/>
                    < Route path='/setting' component={Setting}/>

                    <Route path='/dialogs'
                           // render={() => {
                           //     return <Suspense fallback={<div>Loading...</div>}>                 {/*урок 94*/}
                           //         <DialogsContainer/>
                           //     </Suspense>
                           // }}
                           render={withSuspense(DialogsContainer)}
                    />

                    <Route
                        path='/profile/:userId?'            /*:userId? - это params (урок 60) ? - params необязательный*/
                        // render={() => {
                        //     return <Suspense fallback={<div>Loading...</div>}>                 {/*урок 94*/}
                        //         <ProfileContainer/>
                        //     </Suspense>
                        // }}
                        render={withSuspense(ProfileContainer)}
                    />

                    <Route path='/users'
                           render={() => <UsersContainer/>}
                    />

                    <Route path='/login'
                           render={() => <LoginPage/>}
                    />
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})

export default compose(
    withRouter,
    connect(mapStateToProps, {initializeApp}))(App);
