import React from 'react';
import s from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import NameItem from './NameItem/NameItem.jsx';


const Navbar = (props) => {

    let namesElements = props.sideBar.nameFriends.map(n => <NameItem name={n.name} key={n.id} id={n.id}/>);
    return (
        <nav className={s.nav}>
            <div className={s.item}>
                <NavLink to="/profile" activeClassName={s.activeLink}>Profile</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/dialogs" activeClassName={s.activeLink}>Messages</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/users" activeClassName={s.activeLink}>Usesrs</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/news" activeClassName={s.activeLink}>News</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/music" activeClassName={s.activeLink}>Music</NavLink>
            </div>
            <div className={s.item}>
                <NavLink to="/setting" activeClassName={s.activeLink}>Setting</NavLink>
            </div>


            <h3>Friends</h3>
            <div className={s.namefriends}>
                {namesElements}
            </div>
        </nav>
    );
}

export default Navbar;